.not_found{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: calc(100vh - 60px);
}

.not_found h1{
    font-size: 100px;

}

.not_found a{
    text-decoration: none;
    background-color: #116feb;
    color: white;
    padding: 10px;
    margin-top: 14px;
}